<template>
    <div class='template'>
        <b-row class='statements justify-content-md-center'>
            <h1 class='title col-sm-12'>News!</h1>
            <b-col class='cards col-sm-12'>
                <b-carousel
                    id="carousel-1"
                    v-model="slide"
                    :interval="4000"
                    controls
                    style="text-shadow: 1px 1px 2px #333;"
                    @sliding-start="onSlideStart"
                    @sliding-end="onSlideEnd"
                    img-width="90%"
                    img-height="25%"
                    >
                        <b-carousel-slide
                            style='height: 100%; width: 100%;'
                            v-for="News in NewsList" 
                            :key="News"
                            img-blank>
                            <img
                                v-if='News.title == "Nvidia"'
                                class='nvidia-inception col-sm-12'
                                src='../../assets/nvidia-inception-program.png'>
                            <b-card-text
                                class='news-title'
                                v-if='News.title != "Nvidia"'>
                                {{News.title}}</b-card-text>
                            <div
                              v-for="texts in News.content"
                              :key="texts">
                              <b-card-text
                                class='news-text'>
                                <p v-html=texts></p></b-card-text>
                            </div>
                            
                        </b-carousel-slide>
                        
                </b-carousel>
            </b-col>
            <!-- <b-row class='container cards col-sm-12'>
                <b-card-group deck class='news-card'>
                    <b-card
                      v-for="NewTech in NewTechList" 
                      :key="NewTech"
                      bg-variant='transparent'
                      border-variant="danger"
                      class='news-card-text col-sm-6'
                      :title=NewTech.title>
                        <b-card-text>
                        {{NewTech.content}}</b-card-text>
                    </b-card>
                </b-card-group>
            </b-row> -->
        </b-row>
    </div>
</template>

<script>
import newsJSON from '../../assets/texts/news.json'

export default {
    data() {
        return {
            'news': newsJSON
        }
    },
    computed: {
        NewsList() {
            return newsJSON.News.map((NewsList) => {
                return NewsList
            })
        },
        NewTechList() {
            return newsJSON.NewTech.map((NewTechList) => {
                return NewTechList
            })
        }
    }
}
</script>

<style scoped>
a {
    color: lightskyblue;
}

.template {
    margin: 0vh 0vw 10vh 0vw;
}

.statements {
    padding: 3vh 20vw 3vh 20vw;
    background-color: rgba(52,58,64,1);
    color: white;
    font-family: 'Raleway';
    font-weight: bold;
}

.title {
    text-align: left;
    margin-left: 5vw;
    /* margin: 0vh 1vw 0vh 1vw; */
}

.cards{
    padding: 0vh 0vw 0vh 0vw;
    vertical-align: text-top;
}

.news-title {
    font-size: 150%;
}

.news-text {
    font-size: 100%;
}

.nvidia-inception {
    /* top: -30%; */
    max-width: 30%;
    margin-bottom: 2%;
}

.news-card {
    color: white;
    margin: 0vh 1vw 0vh 1vw;
    font-weight: normal;
    text-align: left;
}

</style>