<template>
    <div>

        <div class='content'>
            <!-- OPENING / PROFILE STATEMENT -->
            <div class='row'>
                <div id='home' class='container opening-title col-sm-12'>
                    <h1>{{this.content.openingStatements.title}}</h1>
                    <h2 v-for='texts in content.openingStatements.content' :key='texts'>
                        {{texts}}
                    </h2>
                    <img src='../../assets/shapes/shape_1.png'
                        style='
                            z-index: -150;
                            position: absolute;
                            top: -5vh;
                            left: 0vw;
                            max-height: 130%;
                            width: 60vw;
                            opacity: 0.2;'>
                </div>
                <div id='home' class='container vision-statements col-sm-12'>
                    <h1>{{this.content.visionStatements.title}}</h1>
                    <h2 v-for='texts in content.visionStatements.content' :key='texts'>
                        {{texts}}
                    </h2>
                    <img src='../../assets/shapes/shape_3.png'
                        style='
                            z-index: -150;
                            position: absolute;
                            top: -5vh;
                            left: 50vw;
                            max-height: 130%;
                            width: 50vw;
                            opacity: 0.2;'>
                </div>
            </div>
        </div>

        <news-page id='news'></news-page>

        <!-- YOUTUBE VIDEO PREVIEWS -->
        <youtube-embed id='youtube'></youtube-embed>

        <!-- TECHNOLOGIES CARDS -->
        <technologies-page id='technologies'></technologies-page>

        <!-- OUR FOUNDERS CARDS -->
        <about-page id='about'></about-page>

    </div>
</template>

<script>
import {bus} from '../../main'

import TechnologiesPage from '../Technologies/TechnologiesPage'
import YouTubeEmbed from '../VideoComponents/YouTubeEmbed'
import AboutPage from '../AboutPage/AboutPage'
import NewsPage from '../NewsPage/NewsPage'

import contentJSON from '../../assets/texts/content.json'

export default {
    components: {
        'technologies-page': TechnologiesPage,
        'youtube-embed': YouTubeEmbed,
        'about-page': AboutPage,
        'news-page': NewsPage,
    },
    data() {
        return {
            content: contentJSON,
            scrollStatus: {
                HomePage: false,
                NewsPage: false,
                YoutubeEmbed: false,
                TechnologiesPage: false,
                AboutPage: false
            }
        }
    },
    methods: {
        scrollToHome() {
            // from Header-1.vue
            bus.$on('scrollToHome', (data) => {
                this.scrollStatus.HomePage = data
            
                if (this.scrollStatus.HomePage == true) {
                    var el = document.getElementById("home");
                    setTimeout(function(){ 
                        el.scrollIntoView({behavior: "smooth", block: "center", inline: "center"})
                        }, 20);
                }

                this.scrollStatus.HomePage = false
            })
        },
        scrollToTechnologies() {
            // from Header-1.vue
            bus.$on('scrollToTechnologies', (data) => {
                this.scrollStatus.TechnologiesPage = data
            
                if (this.scrollStatus.TechnologiesPage == true) {
                    var el = document.getElementById("technologies");
                    setTimeout(function(){ 
                        el.scrollIntoView({behavior: "smooth", block: "center", inline: "center"})
                        }, 20);
                }

                this.scrollStatus.TechnologiesPage = false
            })
        },
        scrollToNews() {
            // from Header-1.vue
            bus.$on('scrollToNews', (data) => {
                this.scrollStatus.NewsPage = data
            
                if (this.scrollStatus.NewsPage == true) {
                    var el = document.getElementById("news");
                    setTimeout(function(){ 
                        el.scrollIntoView({behavior: "smooth", block: "center", inline: "center"})
                        }, 20);
                }

                this.scrollStatus.NewsPage = false
            })
        },
        scrollToAbout() {
            // from Header-1.vue
            bus.$on('scrollToAbout', (data) => {
                this.scrollStatus.AboutPage = data
            
                if (this.scrollStatus.AboutPage == true) {
                    var el = document.getElementById("about");
                    setTimeout(function(){ 
                        el.scrollIntoView({behavior: "smooth", block: "center", inline: "center"})
                        }, 20);
                }

                this.scrollStatus.AboutPage = false
            })
        }
    },
    created() {
        this.scrollToHome()
        this.scrollToNews()
        this.scrollToTechnologies()
        this.scrollToAbout()
    },
}
</script>

<style scoped>

.opening-title {
    color: black;
    max-height: 90%;
    max-width: 100%;
    margin: 20vh 0vw 0vh 0vw;
    padding: 0vh 45vw 0vh 15vw;
    font-family: Raleway;
} .opening-title h1 {
      font-size: 6vh;
      margin: 0vh 0vw 0vh 0vw;
      padding: 0vh 10vw 0vh 0vw;
      text-align: left;
  }
  .opening-title h2 {
      font-size: 2.2vh;
      line-height: 1.5;
      padding: 2vh 0vw 0vh 0vw;
      text-align: left;
  }

.vision-statements {
    color: black;
    max-height: 90%;
    max-width: 100%;
    margin: 5vh 0vw 20vh 0vw;
    padding: 0vh 15vw 0vh 55vw;
    font-family: Raleway;
} .vision-statements h1 {
      font-size: 6vh;
      margin: 0vh 0vw 0vh 0vw;
      padding: 0vh 0vw 0vh 0vw;
      text-align: right;
  }
  .vision-statements h2 {
      font-size: 2.2vh;
      line-height: 1.5;
      padding: 2vh 0vw 0vh 0vw;
      text-align: right;
  }

</style>