<template>
    <div class='main'>
        <div class='row justify-content-md-center'>
            <span class='header-left header-menu col-sm-4 row'>
                <h6 @click='scrollToAbout()' class='col-sm-5'>Contact</h6>
                <span class='col-sm-1'></span>
                <h6 @click='clickMethod(); scrollToHome()' class='col-sm-5'>Home</h6>
            </span>
            
            <span @click='clickMethod()' class='rkb-title col-sm-4'>
                <!-- <img src='../../assets/risetai_logo.png' style='max-width: 10vw;'> -->
                <img @click='clickMethod(); scrollToHome()' src='../../assets/risetai_logo.png' style='max-width: 12vw;'>
            </span>

            <span class='header-right header-menu col-sm-4 row'>
                <h6 @click='scrollToNews()' class='col-sm-5'>News</h6>
                <span class='col-sm-1'></span>
                <h6 @click='scrollToTechnologies()' class='col-sm-5'>Technology</h6>
            </span>

        </div>
    </div>
</template>

<script>

import {bus} from '../../main'

export default {
    methods: {
        clickMethod() {
            this.$router.push('/');
            // window.open('http://example.com');
        },
        scrollToTop() {
            window.scrollTo({
                top: 0,
                left: 0,
                behavior: 'smooth'
            });
        },
        scrollToBottom() {
            window.scrollTo({
              top: document.body.scrollHeight || document.documentElement.scrollHeight,
              left: 0,
              behavior: 'smooth'
            });
            // window.scrollTo({0, document.body.scrollHeight || document.documentElement.scrollHeight, behavior: 'smooth'});
        },
        scrollToHome() {
            bus.$emit('scrollToHome', true)
        },
        scrollToNews() {
            bus.$emit('scrollToNews', true)
        },
        scrollToTechnologies() {
            bus.$emit('scrollToTechnologies', true)
        },
        scrollToAbout() {
            bus.$emit('scrollToAbout', true)
        }
    }
}
</script>

<style scoped>
.main {
    /* background-color:rgba(255,255,255,0.2); */
}

.rkb-title {
    text-align: center;
    align-self: center;
    /* width: 50%; */
    /* height: 100%; */
} .rkb-title img:hover {
      cursor: pointer;
      background-color:rgba(100,100,100,0.8);
  }

.header-menu {
    float: center;
} .header-menu h6{
      font-family: 'Raleway';
      font-weight: bold;
      cursor: pointer;
      color: black;
      background-color:rgba(255,255,255,0.4);
  }
  .header-menu h6:hover {
      font-family: 'Raleway';
      font-weight: bold;
      cursor: pointer;
      color: white;
      background-color:rgba(100,100,100,0.8);
  }

.header-left {
    float: left;
    align-content: center;
}

.header-right {
    float: right;
    align-content: center;
}

</style>